function __gltf_fix_bug( scene )
{
    scene.traverse( function( child )
    {
        if( child.isMesh )
        {
            // why: 'vColor' differ between VERTEX and FRAGMENT
            var c = child.geometry.attributes.color;
            if( c && c.itemSize === 4 )
            {
                var m = child.material;
                if( m.vertexColors === true )
                {
                    m.vertexColors = false;
                    m.needsUpdate = true;
                }
            }

            // why: vertex shader not compiled (due to 'transformedTangent')
            var g = child.geometry;
            if( g.attributes.tangent )
            {
                delete g.attributes.tangent;
            }
        }
    });
}
function __gltf_add_envmap( scene )
{
    var background = __appWorks__.scene.background;

    if( background && background.isCubeTexture )
    {
        scene.traverse( function( child )
        {
            var m = child.material;
            if( m && ( m.isMeshStandardMaterial || (m.isShaderMaterial && m.envMap !== undefined) ) )
            {
                m.envMap = background;
                m.needsUpdate = true;
            }
        });
    }
}

const Loader = function( appWorks )
{
    this.appWorks = appWorks;// <=== !!

    const scope = this; // loader

    //======================
    // load files
    //======================

    this.imageFiles = []; // used for md2
    this.mtlFile = null; // used for obj & mtl

	this.loadItemList = function ( items ) {

		LoaderUtils.getFilesFromItemList( items, function ( files, filesMap ) {

			scope.loadFiles( files, filesMap );

		} );

	};

	this.loadFiles = function ( files, filesMap ) {

		if ( files.length > 0 ) {

			var filesMap = filesMap || LoaderUtils.createFilesMap( files );

            var manager = new THREE.LoadingManager();
            manager.onStart    = () => { createLoadingSpinner(); }
            manager.onProgress = (url, itemsLoaded, itemsTotal) => { updateLoadingSpinner( itemsLoaded, itemsTotal ); }
            manager.onLoad     = () => { hideLoadingSpinner(); } // cf: removeLoadingSpinner()
            manager.onError    = url => { errorLoadingSpinner('There was an error loading ' + url); }
			manager.setURLModifier( function ( url ) {

                url = url.replace( /^(\.?\/)/, '' ); // remove './'

				var file = filesMap[ url ];

				if ( file ) {

					console.log( 'Loading', url );

					return URL.createObjectURL( file );

				}

				return url;

			});
            manager.addHandler( /\.tga$/i, new TGALoader() );

            //

            this.imageFiles = [];
            this.mtlFile = null;

            for ( var i = 0; i < files.length; i ++ ) {

                if( files[ i ].name.match(/\.(png|jpg|gif)$/i) )
                {
                    this.imageFiles.push( files[ i ] ); // needed for md2
                }

                else if( files[ i ].name.match(/\.(mtl)$/i) )
                {
                    this.mtlFile = files[ i ];
                }

			}

            //

			for ( var i = 0; i < files.length; i ++ ) {

                this.loadFile( files[ i ], manager );

			}

		}

	};

	this.loadFile = function ( file, manager ) {

		var filename = file.name;
		var extension = filename.split( '.' ).pop().toLowerCase();

		var reader = new FileReader();
		reader.addEventListener( 'progress', function ( event ) {

			var size = '(' + Math.floor( event.total / 1000 ).toLocaleString() + ' KB)';
			var progress = Math.floor( ( event.loaded / event.total ) * 100 ) + '%';

			console.log( 'Loading', filename, size, progress );

		} );

		switch ( extension ) {

			case '3ds':

				reader.addEventListener( 'load', function ( event ) {

                    var loader = new TDSLoader( manager );
                    var object = loader.parse( event.target.result );

                    object.name = filename;

                    addObject( object );

				}, false );
				reader.readAsArrayBuffer( file );

                break;
                
            case '3mf':

                reader.addEventListener( 'load', function ( event ) {

                    var loader = new ThreeMFLoader( manager );

                    // loader.addExtension( ThreeMFLoader.MaterialsAndPropertiesExtension );

                    var object = loader.parse( event.target.result );
                    
                    object.name = filename;

                    addObject( object );

				}, false );
				reader.readAsArrayBuffer( file );

                break;

			case 'amf':

				reader.addEventListener( 'load', function ( event ) {

					var loader = new AMFLoader( manager );
                    var object = loader.parse( event.target.result );
                    
                    object.name = filename;

                    addObject( object );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			case 'dae':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var loader = new ColladaLoader( manager );
					var collada = loader.parse( contents );

					collada.scene.name = filename;

					addAnimation( collada.scene, collada.animations );
                    addObject( collada.scene );

				}, false );
				reader.readAsText( file );

				break;

			case 'drc':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var loader = new DRACOLoader( manager );
					loader.setDecoderPath( __dracoDecoderPath__ );
					loader.parse( contents, function ( geometry ) {

                        geometry.center(); // jamie
                        computeVertexNormals( geometry );; // jamie

                        var object;

						if ( geometry.index !== null ) {

							var material = new THREE.MeshStandardMaterial();

							object = new THREE.Mesh( geometry, material );
							object.name = filename;

						} else {

							var material = new THREE.PointsMaterial( { size: 0.01 } );

							if ( geometry.hasAttribute( 'color' ) === true ) material.vertexColors = true;

							object = new THREE.Points( geometry, material );
							object.name = filename;

						}

                        loader.dispose();
                        addObject( object );

					} );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			case 'fbx':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var loader = new FBXLoader( manager );
                    var object = loader.parse( contents );
                    
                    object.name = filename;

					addAnimation( object, object.animations );
                    addObject( object );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

            case 'md2':

                reader.addEventListener( 'load', function ( event ) {

                    var contents = event.target.result;

                    var geometry = new MD2Loader( manager ).parse( contents );
                    var texture = null;
                    if( scope.imageFiles.length === 1 ) // cf: one md2 model has only one texture
                    {
                        texture = new THREE.TextureLoader().load( URL.createObjectURL( scope.imageFiles[0] ) );
                    }
                    var material = new THREE.MeshStandardMaterial({ map: texture });
                    var mesh = new THREE.Mesh( geometry, material );

                    mesh.mixer = new THREE.AnimationMixer( mesh );                    
                    mesh.name = filename;

                    addAnimation( mesh, geometry.animations );
                    addObject( mesh );

                }, false );
                reader.readAsArrayBuffer( file );

                break;

			case 'glb':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

                    const loader = createGLTFLoader( manager );

					loader.parse( contents, '', function ( result ) {

						var scene = result.scene;
						scene.name = filename;

                        __gltf_fix_bug( scene ); // added by jamie
                        __gltf_add_envmap( scene ); // added by jamie

						addAnimation( scene, result.animations );
                        addObject( scene );

                        loader.dracoLoader.dispose();
						loader.ktx2Loader.dispose();

					} );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			case 'gltf':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

                    const loader = createGLTFLoader( manager );

					loader.parse( contents, '', function ( result ) {

						var scene = result.scene;
						scene.name = filename;

                        __gltf_fix_bug( scene ); // added by jamie
                        __gltf_add_envmap( scene ); // added by jamie

						addAnimation( scene, result.animations );
                        addObject( scene );

                        loader.dracoLoader.dispose();
						loader.ktx2Loader.dispose();

					} );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			case 'js':
			case 'json':

				reader.addEventListener( 'load', function ( event ) {
					var contents = event.target.result;
					// 2.0
					if ( contents.indexOf( 'postMessage' ) !== - 1 )
                    {
						var blob = new Blob( [ contents ], { type: 'text/javascript' } );
						var url = URL.createObjectURL( blob );
						var worker = new Worker( url );
						worker.onmessage = function ( event )
                        {
							event.data.metadata = { version: 2 };
							handleJSON( event.data );
						};
						worker.postMessage( Date.now() );
						return;
					}
					// >= 3.0
					var data;
					try {
						data = JSON.parse( contents );
					} catch ( error ) {
						alert( error );
						return;
					}
					handleJSON( data );
				}, false );
				reader.readAsText( file );

				break;

			case 'kmz':

				reader.addEventListener( 'load', function ( event ) {

					var loader = new KMZLoader( manager );
					var collada = loader.parse( event.target.result );

					collada.scene.name = filename;

                    addObject( collada.scene );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			case 'obj':

                function _loadOBJ( fileOBJ, materials=null )
                {
                    reader.addEventListener( 'load', function ( event )
                    {
                        const contents = event.target.result;

                        const loader = new OBJLoader( manager );
                        if( materials )
                        {
                            loader.setMaterials( materials )
                        }
                        const object = loader.parse( contents );

                        object.name = filename;

                        addObject( object );

                    }, false );
                    reader.readAsText( fileOBJ );
                }

                function _load_OBJ_MTL( fileOBJ, fileMTL )
                {
                    if( fileMTL )
                    {
                        var _reader = new FileReader();
                        _reader.addEventListener( 'load', function ( event ) {

                            const contents = event.target.result;

                            const materials = new MTLLoader( manager ).parse( contents );
                            materials.preload();

                            console.log( 'Loading', fileMTL.name );

                            //=====================================================
                            _loadOBJ( fileOBJ, materials );
                            //=====================================================

                        }, false );
                        _reader.readAsText( fileMTL );
                    }
                    else
                    {
                        //=====================================================
                        _loadOBJ( fileOBJ );
                        //=====================================================
                    }
                }

                _load_OBJ_MTL( file, this.mtlFile );

                break;

			case 'ply':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var geometry = new PLYLoader( manager ).parse( contents );

                    computeVertexNormals( geometry );

					var material = new THREE.MeshStandardMaterial();

					var mesh = new THREE.Mesh( geometry, material );
					mesh.name = filename;

                    addObject( mesh );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			case 'stl':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var geometry = new STLLoader( manager ).parse( contents );

                    computeVertexNormals( geometry );

                    var material;
                    if( geometry.hasColors )
                    {
                        material = new THREE.MeshStandardMaterial({ vertexColors: true });
                    }
                    else
                    {
                        material = new THREE.MeshStandardMaterial();
                    }

					var mesh = new THREE.Mesh( geometry, material );
					mesh.name = filename;

                    addObject( mesh );

				}, false );

				if ( reader.readAsBinaryString !== undefined ) {

					reader.readAsBinaryString( file );

				} else {

					reader.readAsArrayBuffer( file );

				}

				break;

			case 'svg':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var loader = new SVGLoader( manager );
					var paths = loader.parse( contents ).paths;

					//

					var group = new THREE.Group();
					group.scale.multiplyScalar( 0.1 );
					group.scale.y *= - 1;

					for ( var i = 0; i < paths.length; i ++ ) {

						var path = paths[ i ];

						var material = new THREE.MeshBasicMaterial( {
							color: path.color,
							depthWrite: false
						} );

                        var shapes = SVGLoader.createShapes( path );

						for ( var j = 0; j < shapes.length; j ++ ) {

							var shape = shapes[ j ];

                            var geometry = new THREE.ShapeGeometry( shape );
							var mesh = new THREE.Mesh( geometry, material );

							group.add( mesh );

						}

                    }
                    
                    group.name = filename;

                    addObject( group );

				}, false );
				reader.readAsText( file );

				break;

			case 'vtk':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

					var geometry = new VTKLoader( manager ).parse( contents );

                    computeVertexNormals( geometry );

					var material = new THREE.MeshStandardMaterial();

					var mesh = new THREE.Mesh( geometry, material );
					mesh.name = filename;

                    addObject( mesh );

				}, false );
                reader.readAsArrayBuffer( file );

				break;

            case 'vox':

                reader.addEventListener( 'load', function ( event ) {

                    var contents = event.target.result;

                    var chunks = new VOXLoader( manager ).parse( contents );

                    var meshes = chunks.map( chunk => new VOXMesh( chunk ) );

                    var object = new THREE.Group();
                    object.add( ...meshes );
                    object.name = filename;

                    addObject( object );

                }, false );
                reader.readAsArrayBuffer( file );

                break;

			case 'wrl':

				reader.addEventListener( 'load', function ( event ) {

					var contents = event.target.result;

                    var result = new VRMLLoader( manager ).parse( contents );

                    // NOTE*:
                    // 1) result ==> THREE.Scene
                    // 2) every geometry inside result ==> THREE.BufferGeometry
                    // 3) every material inside result ==> THREE.MeshBasicMaterial or THREE.MeshPhongMaterial
                    // 4) color-priority: material.map >> material.vertexColors >> material.color

                    // // color-priority (defined by VRML)
                    // result.traverse( function( child )
                    // {
                    //     if( child.isMesh )
                    //     {
                    //         // 1st color-priority
                    //         if( child.material.map )
                    //         {
                    //             child.material.color.setHex(0xffffff);
                    //             child.material.vertexColors = false;
                    //         }
                    //         // 2nd color-priority
                    //         else if( child.geometry.attributes.color )
                    //         {
                    //             child.material.vertexColors = THREE.VertexColors;
                    //             child.material.color.setHex(0xffffff);
                    //         }
                    //     }
                    // });

                    result.name = filename;

                    // addScene( result );
                    addObject( result );

				}, false );
				reader.readAsText( file );

				break;

			case 'zip':

				reader.addEventListener( 'load', function ( event ) {

					handleZIP( event.target.result );

				}, false );
				reader.readAsArrayBuffer( file );

				break;

			default:
                if( !['png', 'jpg', 'gif', 'bmp', 'tga', 'mtl', 'bin'].includes( extension ) )
                {
                    console.log( 'Unsupported 3D file format:', extension );
                }
				break;

		}

    };

    function addAnimation( object, animations )
    {
        if( animations && animations.length > 0 ) object.animations.push( ...animations );
    }
    
    function addObject( object )
    {
        __appWorks__ = scope.appWorks;// <=== !!

        add( object );

        welcomeObject( object );
    }

    function addScene( scene )
    {
        __appWorks__.setScene( scene );
    }

	function handleJSON( data )
    {
		if( data.metadata === undefined ) { // 2.0
			data.metadata = { type: 'DiscreteGeometry' };
		}

		if( data.metadata.type === undefined ) { // 3.0
			data.metadata.type = 'DiscreteGeometry';
		}

		if( data.metadata.formatVersion !== undefined ) {
			data.metadata.version = data.metadata.formatVersion;
		}

		switch( data.metadata.type.toLowerCase() )
        {
			case 'buffergeometry':
				var loader = new THREE.BufferGeometryLoader();
				var result = loader.parse( data );
				var mesh = new THREE.Mesh( result );
                addObject( mesh );
				break;

			case 'discretegeometry':
				console.error( 'Loader: "DiscreteGeometry" is no longer supported.' );
				break;

			case 'object':
				var loader = new THREE.ObjectLoader();
				loader.parse( data, function ( result )
                {
					if ( result.isScene )
                    {
                        addScene( result );
					}
                    else
                    {
                        if( result.name === 'natureObject' )
                        {
                            result.children.forEach( function( child )
                            {
                                add( cloneObject( child ) );
                            });
                        }
                        else
                        {
                            addObject( result );
                        }
					}
				});
				break;

			case 'app':
				__appWorks__.fromJSON( data );
				break;
		}

	}

	function handleZIP( contents ) {

		const zip = unzipSync( new Uint8Array( contents ) );

        const manager = new THREE.LoadingManager();
        manager.onStart    = () => { createLoadingSpinner(); }
        manager.onProgress = (url, itemsLoaded, itemsTotal) => { updateLoadingSpinner( itemsLoaded, itemsTotal ); }
        manager.onLoad     = () => { hideLoadingSpinner(); } // cf: removeLoadingSpinner()
        manager.onError    = url => { errorLoadingSpinner('There was an error loading ' + url); }
        manager.setURLModifier( ( url ) =>
        {
            const file = zip[ url ];
            if( file )
            {
                console.log( 'Loading', url );
                const blob = new Blob( [ file.buffer ], { type: 'application/octet-stream' } );
                return URL.createObjectURL( blob );
            }
            return url;
        });
        manager.addHandler( /\.tga$/i, new TGALoader() );

        // OBJ & MTL
        let bufferMTL = null, bufferOBJ = null;

		for( let path in zip )
        {
            const file = zip[ path ];

            // data from the file
            // 1) binary_data = file.buffer
            // 2) string_data = strFromU8( file )

			const extension = path.split( '.' ).pop().toLowerCase();

			switch( extension )
            {
                case '3ds':
                {
                    const loader = new TDSLoader( manager );
                    const object = loader.parse( file.buffer );
                    object.name = path;
                    addObject( object );
                    break;
                }

                case '3mf':
                {
                    const loader = new ThreeMFLoader( manager );
                    // loader.addExtension( ThreeMFLoader.MaterialsAndPropertiesExtension );
                    const object = loader.parse( file.buffer );
                    object.name = path;
                    addObject( object );
                    break;
                }

                case 'amf':
                {
					const loader = new AMFLoader( manager );
                    const object = loader.parse( file.buffer );
                    object.name = path;
                    addObject( object );
				    break;
                }

                case 'dae':
                {
					const loader = new ColladaLoader( manager );
					const collada = loader.parse( strFromU8( file ) );
					collada.scene.name = path;
					addAnimation( collada.scene, collada.animations );
                    addObject( collada.scene );
				    break;
                }

                case 'drc':
                {
					const loader = new DRACOLoader( manager );
					loader.setDecoderPath( __dracoDecoderPath__ );
					loader.parse( file.buffer, function ( geometry ) {

                        geometry.center(); // jamie
                        computeVertexNormals( geometry );; // jamie

                        let object;

						if ( geometry.index !== null )
                        {
							const material = new THREE.MeshStandardMaterial();
							object = new THREE.Mesh( geometry, material );
							object.name = path;
						}
                        else
                        {
							const material = new THREE.PointsMaterial( { size: 0.01 } );
							if ( geometry.hasAttribute( 'color' ) === true ) material.vertexColors = true;
							object = new THREE.Points( geometry, material );
							object.name = path;
						}
                        loader.dispose();
                        addObject( object );
					});
				    break;
                }

				case 'fbx':
                {
                    const loader = new FBXLoader( manager );
                    const object = loader.parse( file.buffer );
                    object.name = path;
                    addAnimation( object, object.animations );
                    addObject( object );
                    break;
                }

                case 'md2':
                {
                    const loader = new MD2Loader( manager );
                    const geometry = loader.parse( file.buffer );
                    let texture = null;
                    for( let path in zip )
                    {
                        let ext = path.split( '.' ).pop().toLowerCase();
                        if( ext === 'jpg' ) ext = 'jpeg';
                        if( ['png','jpeg','gif'].includes( ext ) )
                        {
                            const file = zip[ path ];
                            const mimeType = 'image/' + ext;
                            const blob = new Blob( [ file.buffer ], { type: mimeType } );
                            const url = URL.createObjectURL( blob );
                            texture = new THREE.TextureLoader().load( url );
                            break; // cf: one md2 model has only one texture
                        }
                    }
                    const material = new THREE.MeshStandardMaterial({ map: texture });
                    const mesh = new THREE.Mesh( geometry, material );
                    mesh.mixer = new THREE.AnimationMixer( mesh );                    
                    mesh.name = path;
                    addAnimation( mesh, geometry.animations );
                    addObject( mesh );
                    break;
                }

				case 'glb':
                {
                    const loader = createGLTFLoader( manager );
					loader.parse( file.buffer, '', function ( result )
                    {
						const scene = result.scene;
                        scene.name = path;
                        __gltf_fix_bug( scene ); // added by jamie
                        __gltf_add_envmap( scene ); // added by jamie
						addAnimation( scene, result.animations );
                        addObject( scene );
                        loader.dracoLoader.dispose();
						loader.ktx2Loader.dispose();
					});
					break;
                }

				case 'gltf':
                {
					const loader = createGLTFLoader( manager );
					loader.parse( strFromU8( file ), '', function ( result )
                    {
						const scene = result.scene;
                        scene.name = path;
                        __gltf_fix_bug( scene ); // added by jamie
                        __gltf_add_envmap( scene ); // added by jamie
						addAnimation( scene, result.animations );
                        addObject( scene );
                        loader.dracoLoader.dispose();
						loader.ktx2Loader.dispose();
					});
					break;
                }

                case 'js':
			    case 'json':
                {
					const contents = file.buffer;
					// 2.0
					if ( contents.indexOf( 'postMessage' ) !== - 1 )
                    {
						const blob = new Blob( [ contents ], { type: 'text/javascript' } );
						const url = URL.createObjectURL( blob );
						const worker = new Worker( url );
						worker.onmessage = function ( event )
                        {
							event.data.metadata = { version: 2 };
							handleJSON( event.data );
						};
						worker.postMessage( Date.now() );
						return;
					}
					// >= 3.0
					let data;
					try {
						data = JSON.parse( contents );
					} catch ( error ) {
						alert( error );
						return;
					}
					handleJSON( data );
				    break;
                }

                case 'kmz':
                {
					const loader = new KMZLoader( manager );
					const collada = loader.parse( file.buffer );
					collada.scene.name = path;
                    addObject( collada.scene );
				    break;
                }

                case 'mtl':
                {
                    bufferMTL = file.buffer;
                    break;
                }
                case 'obj':
                {
                    bufferOBJ = file.buffer;
                    break;
                }

                case 'ply':
                {
					const geometry = new PLYLoader( manager ).parse( file.buffer );
                    computeVertexNormals( geometry );
					const material = new THREE.MeshStandardMaterial();
					const mesh = new THREE.Mesh( geometry, material );
					mesh.name = path;
                    addObject( mesh );
				    break;
                }

                case 'stl':
                {
                    const contents = file.buffer; //  contents = strFromU8( file )  ...
					const geometry = new STLLoader( manager ).parse( contents );
                    computeVertexNormals( geometry );
                    let material;
                    if( geometry.hasColors )
                    {
                        material = new THREE.MeshStandardMaterial({ vertexColors: true });
                    }
                    else
                    {
                        material = new THREE.MeshStandardMaterial();
                    }
					const mesh = new THREE.Mesh( geometry, material );
					mesh.name = path;
                    addObject( mesh );
				    break;
                }

                case 'svg':
                {
					const loader = new SVGLoader( manager );
					const paths = loader.parse( file.buffer ).paths;
					const group = new THREE.Group();
					group.scale.multiplyScalar( 0.1 );
					group.scale.y *= - 1;
					for ( let i = 0; i < paths.length; i ++ )
                    {
						const path = paths[ i ];
						const material = new THREE.MeshBasicMaterial( {
							color: path.color,
							depthWrite: false
						} );
                        const shapes = SVGLoader.createShapes( path );
						for ( let j = 0; j < shapes.length; j ++ )
                        {
							const shape = shapes[ j ];
                            const geometry = new THREE.ShapeGeometry( shape );
							const mesh = new THREE.Mesh( geometry, material );
							group.add( mesh );
						}
                    }
                    group.name = path;
                    addObject( group );
				    break;
                }

                case 'vtk':
                {
					const geometry = new VTKLoader( manager ).parse( file.buffer );
                    computeVertexNormals( geometry );
					const material = new THREE.MeshStandardMaterial();
					const mesh = new THREE.Mesh( geometry, material );
					mesh.name = path;
                    addObject( mesh );
				    break;
                }

                case 'vox':
                {
                    const chunks = new VOXLoader( manager ).parse( file.buffer );
                    const meshes = chunks.map( chunk => new VOXMesh( chunk ) );
                    const object = new THREE.Group();
                    object.add( ...meshes );
                    object.name = path;
                    addObject( object );
                    break;
                }

                case 'wrl':
                {
                    const object = new VRMLLoader( manager ).parse( strFromU8( file ) );
                    object.name = path;
                    addObject( object );
				    break;
                }

                case 'zip':
                {
					handleZIP( file.buffer );
				    break;
                }

                default:
                {
                    if( !['png', 'jpg', 'gif', 'bmp', 'tga', 'mtl', 'bin'].includes( extension ) )
                    {
                        console.log( 'Unsupported 3D file format:', extension );
                    }
                    break;
                }
			}
		}

        // OBJ & MTL
        if( bufferMTL && bufferOBJ )
        {
            const materials = new MTLLoader( manager ).parse( strFromU8( bufferMTL ) );
            const object = new OBJLoader( manager ).setMaterials( materials ).parse( strFromU8( bufferOBJ ) );
            addObject( object );
        }
	}

    function createGLTFLoader( manager )
    {
		const dracoLoader = new DRACOLoader();
		dracoLoader.setDecoderPath( __dracoDecoderPath__ );

		const ktx2Loader = new KTX2Loader();
		ktx2Loader.setTranscoderPath( __basisTranscoderPath__ );
        ktx2Loader.detectSupport( __appWorks__.renderer )

		const loader = new GLTFLoader( manager );
        // loader.setCrossOrigin('anonymous');
		loader.setDRACOLoader( dracoLoader );
		loader.setKTX2Loader( ktx2Loader );
		loader.setMeshoptDecoder( MeshoptDecoder );

		return loader;
	}

    //======================
    // open files
    //======================

    this.openFiles = function()
    {
        // div (= input + label)
        const div = document.createElement( 'div' );
        div.style.cssText = 'position: absolute; left: 44%; top: 48%;';

        // input
        const input = document.createElement( 'input' );
        input.id = 'file-input';
        input.type = 'file';
        input.multiple = true;
        input.style.display = 'none';
        input.addEventListener( 'change', () =>
        {
            div.remove();
            if( input.files ) scope.loadFiles( input.files );
        });

        // label
        const label = document.createElement( 'label' );
        label.htmlFor = 'file-input';
        label.style.cssText = 'color: white; background-color: #3276c3; font-weight: bold; cursor: pointer; padding: 0.625rem; border-radius: 0.4rem;';
        label.addEventListener( 'mouseover', () => { label.style.backgroundColor = '#333'; });
        label.addEventListener( 'mouseleave', () => { label.style.backgroundColor = '#3276c3'; });
        label.innerHTML = `
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 17" style="width:1em; height:1em; fill:currentColor; vertical-align: middle; margin-top: -0.25em; margin-right: 0.25em;">
                <path d="M18.125,15.804l-4.038-4.037c0.675-1.079,1.012-2.308,1.01-3.534C15.089,4.62,12.199,1.75,8.584,1.75C4.815,1.75,1.982,4.726,2,8.286c0.021,3.577,2.908,6.549,6.578,6.549c1.241,0,2.417-0.347,3.44-0.985l4.032,4.026c0.167,0.166,0.43,0.166,0.596,0l1.479-1.478C18.292,16.234,18.292,15.968,18.125,15.804 M8.578,13.99c-3.198,0-5.716-2.593-5.733-5.71c-0.017-3.084,2.438-5.686,5.74-5.686c3.197,0,5.625,2.493,5.64,5.624C14.242,11.548,11.621,13.99,8.578,13.99 M16.349,16.981l-3.637-3.635c0.131-0.11,0.721-0.695,0.876-0.884l3.642,3.639L16.349,16.981z"></path>
            </svg>
            <span>Choose files</span>
        `;

        div.appendChild( input );
        div.appendChild( label );
        document.body.append( div );
    }

    //======================
    // drag & drop files...
    //======================

    this.dragDropFiles = function( container = appWorks.dom )
    {
        container.addEventListener('dragover', function( e )
        {
            e.preventDefault();
            e.stopPropagation();
    
            container.classList.add('dragover');
        });
    
        container.addEventListener('dragleave', function( e )
        {
            e.preventDefault();
            e.stopPropagation();
    
            container.classList.remove('dragover');
        });
    
        container.addEventListener('drop', function( e )
        {
            // e.stopPropagation();
            e.preventDefault();
    
            if ( e.dataTransfer.types[ 0 ] === 'text/plain' ) return; // Outliner drop
    
            if ( e.dataTransfer.items ) {
    
                // DataTransferItemList supports folders
    
                scope.loadItemList( e.dataTransfer.items );
    
            } else {
    
                scope.loadFiles( e.dataTransfer.files );
    
            }
    
        }, false );
    }
}

const LoaderUtils = {

	createFilesMap: function ( files ) {

		var map = {};

		for ( var i = 0; i < files.length; i ++ ) {

			var file = files[ i ];
			map[ file.name ] = file;

		}

		return map;

	},

	getFilesFromItemList: function ( items, onDone ) {

		// TOFIX: setURLModifier() breaks when the file being loaded is not in root

		var itemsCount = 0;
		var itemsTotal = 0;

		var files = [];
		var filesMap = {};

		function onEntryHandled() {

			itemsCount ++;

			if ( itemsCount === itemsTotal ) {

				onDone( files, filesMap );

			}

		}

		function handleEntry( entry ) {

            if( !entry ) return;

			if ( entry.isDirectory ) {

				var reader = entry.createReader();
				reader.readEntries( function ( entries ) {

					for ( var i = 0; i < entries.length; i ++ ) {

						handleEntry( entries[ i ] );

					}

					onEntryHandled( entry );

				} );

			} else if ( entry.isFile ) {

				entry.file( function ( file ) {

					files.push( file );

					filesMap[ entry.fullPath.substr( 1 ) ] = file;
					onEntryHandled();

				} );

			}

			itemsTotal ++;

		}

		for ( var i = 0; i < items.length; i ++ ) {

			handleEntry( items[ i ].webkitGetAsEntry() );

		}

	}

}

const loadFiles = function( urls, modelKey, appWorks = __appWorks__ )
// modelKey: needed to decrypt (*.enc) in urls
{
    let secretKey;
    if( modelKey )
    {
        function __sec_from_enc( encKey )
        {
            return encKey.split('').splice(5,44,'sangk').reverse().join('');
        }
        secretKey = __sec_from_enc( modelKey );
    }

    const promises = urls.map( async url =>
    {
        const res = await fetch( url );
        let blob = await res.blob();

        let filename;
        if( url.split('.').pop() === 'enc' )
        {
            if( modelKey )
            {
                blob = await decryptFile( secretKey, blob );
            }
            else
            {
                blob = null;
            }
            filename = url.replace('.enc', '');
        }
        else
        {
            filename = url;
        }
        filename = filename.split('/').pop().split('?')[ 0 ];

        return ( blob ) ? new File( [ blob ], filename ) : null;
    });

    Promise.all( promises ).then( files =>
    {
        files = files.filter( file => file ); // remove nulls existing inside the files

        const loader = new Loader( appWorks );

        loader.loadFiles( files );
    });
}

const openFiles = function( appWorks = __appWorks__ )
{
    if( !__loader__ ) __loader__ = new Loader( appWorks );
    __loader__.openFiles();
}

const dragDropFiles = function( appWorks = __appWorks__ )
{
    if( !__loader__ ) __loader__ = new Loader( appWorks );
    __loader__.dragDropFiles( appWorks.dom );
}

