const computeVertexNormals = function( geometry )
// create vertex normals of geometry if they do not exist
{
    var normalExist = false;

    if( geometry.isDiscreteGeometry === true )
    {
        var vNormals = geometry.faces[ 0 ].vertexNormals;
        if( vNormals && vNormals.length === 3 ) normalExist = true;
    }
    else if( geometry.isBufferGeometry === true )
    {
        var aNormal = geometry.attributes.normal;
        if( aNormal && aNormal.count > 0 ) normalExist = true;
    }

    if( normalExist === false )
    {
        geometry.computeVertexNormals();
    }
}

function __raymarch_getBoundingBox( raymarch )
{
    const points = raymarch.toPoints( 0.1, false );
    points.geometry.computeBoundingBox();
    raymarch.geometry.boundingBox = points.geometry.boundingBox.clone();
    dispose( points );
    return raymarch.geometry.boundingBox;
}
function __instMesh_getBoundingBox( instMesh )
{
    const geometry = instMesh.geometry;
    if( geometry.boundingBox === null ) geometry.computeBoundingBox();
    const baseBox = geometry.boundingBox;

    const box = new THREE.Box3();
    const unionBox = new THREE.Box3();
    const matrix = new THREE.Matrix4();
    const count = instMesh.count;

    for( var i = 0; i < count; i++ )
    {
        instMesh.getMatrixAt( i, matrix );
        box.copy( baseBox ).applyMatrix4( matrix );
        unionBox.union( box );
    }

    return unionBox;
}
const getBoundingBox = function( object )
{
    if( object.isRaymarch )
    {
        return __raymarch_getBoundingBox( object );
    }
    else if( object.isInstancedMesh === true )
    {
        return __instMesh_getBoundingBox( object );
    }
    else
    {
        return new THREE.Box3().setFromObject( object );
    }
}

const getVertexFaceCount = function( object )
{
    let nvertices = 0, nfaces = 0;

    if( object.isDiscreteGeometry )
    {
        nvertices = object.vertices.length;
        nfaces = object.faces.length;
    }

    else if( object.isBufferGeometry )
    {
        nvertices = object.attributes.position.count;
        nfaces = object.index ? object.index.count / 3 : object.attributes.position.count / 3;
    }

    else if( object.isObject3D )
    {
        object.traverse( child =>
        {
            if( child.isMesh )
            {
                let count = getVertexFaceCount( child.geometry );
                nvertices += count.nvertices;
                nfaces += count.nfaces;
            }
        });
    }

    return { nvertices, nfaces };
}

const getTextureCount = function( object )
{
    let ntextures = 0;
    traverseMaterials( object, material =>
    {
        let keys = Object.keys( material ).filter( key => key.match(/map$/i) );
        keys = keys.filter( key => material[ key ] );
        ntextures += keys.length;
    });
    return ntextures;
}

const getMaterialCount = function( object )
{
    const materials = new Set();

    object.traverse( child =>
    {
        if ( child.material ) materials.add( child.material );
    });

    return materials.size;
}

