const singleAppWorks = ( divID, modelKey ) =>
// 1) <div id="model_name"></div> (or nothing)
// 2) <div id="car" data-url="models/car.glb"></div>
// 3) <div id="cars" 
//      style="width:500px; height:500px" 
//      data-img="images/car_background.jpg" 
//      data-url="models/car_01.glb,
//                models/car_02.zip,
//                models/car_03.zip,...">
//    </div>
{
    const dom = document.getElementById( divID );

    const appWorks = new AppWorks({
        dom: dom,
        width: parseInt( dom?.style?.width, 10 ) || window.innerWidth,
        height: parseInt( dom?.style?.height, 10 ) || window.innerHeight,
        background: dom?.getAttribute( 'data-img' ) || 'default.hdr',
        // background: 0x191919,
        // background: 'default.hdr',
        // background: 'images/bg_black_leather.jpg',
    });

    if( !dom )
    {
        document.body.appendChild( appWorks.dom );
    }

    appWorks.init().animate();

    let urls = dom?.getAttribute( 'data-url' );
    if( urls )
    {
        urls = urls.replace(/\r?\n|\r/g,'').split(',').map( x => x.replace(/ /g,'') );
        loadFiles( urls, modelKey );
    }

    dragDropFiles( appWorks );
}

const multipleAppWodrks = ( divID, canvasID, modelKey ) =>
// <canvas id="myCanvas"></canvas>
// <div id="myModels">
//   <div style="width:300px; height: 300px;" data-url="models/model_01.glb"></div>
//   <div style="width:300px; height: 300px;" data-url="models/model_02.glb"></div>
//   <div style="width:300px; height: 300px;" data-url="models/model_03.glb"></div>
//   ...
// </div>
{
    const divs = document.getElementById( divID );
    const canvas = document.getElementById( canvasID );

    if( !divs )
    {
        console.warn(">> cannot find 'div' containing models...");
        return;
    }
    else if( divs.childElementCount === 0 )
    {
        console.warn(`>> '${divID}' has no children...`);
        return;
    }
    else if( !canvas )
    {
        console.warn(">> cannot find 'canvas' to render models...");
        return;
    }

    divs.style.position = 'absolute';
    canvas.style.cssText = "position:absolute; left:0; width:100%; height:100%;";

    __appWorkss__ = [];

    for( const dom of divs.children )
    {
        const color = randomHexColor({ colsys: 'hsl', h: [0.0, 1.0], s: [0.1, 0.2], l: [0.5, 0.8] });

        const appWorks = new AppWorks({
            dom: dom,
            width: parseInt( dom?.style?.width, 10 ) || 300,
            height: parseInt( dom?.style?.height, 10 ) || 300,
            background: dom?.getAttribute( 'data-img' ) || color,
            multiscene: canvasID
        });
        appWorks.init();

        let urls = dom?.getAttribute( 'data-url' ); // dom.dataset.url
        if( urls )
        {
            urls = urls.replace(/\r?\n|\r/g,'').split(',').map( x => x.replace(/ /g,'') );
            loadFiles( urls, modelKey, appWorks );
        }

        dragDropFiles( appWorks );

        __appWorkss__.push( appWorks );
    }

    animate();
}

const renderModels = ( options = {} ) =>
// renderModels();           // no model (but drag & drop available)
// renderModels('model');    // needed the div of id = 'model'
// renderModels({            // single model
//     id: 'model',          // div id
//     img: '.../image.jpg', // background
//     url: '.../model.zip', // model url
// });
// renderModels({                                 // multiple models
//     id: 'models',                              // div id
//     style: 'width:800px; height:600px',        // viewport size
//     url: '.../model_01.glb, .../model_02.zip', // model urls
// });
// renderModels({    // multiple viewports
//     id: 'models', // div id
//     children: [   // 1 child model per viewport
//         { style: "width:300px; height: 300px;", url: ".../model_01.glb" },
//         { style: "width:300px; height: 300px;", url: ".../model_02.vox" },
//         ...
//     ]
// });
{
    if( typeof options === 'string' )
    {
        options = { id: options };
    }

    const { id, img, url, style, children, modelKey } = options;

    let div = document.getElementById( id );
    if( !div )
    {
        div = document.createElement('div');
        div.id = id;
        document.body.appendChild( div );
    }

    if( img )   div.dataset.img = img;
    if( url )   div.dataset.url = url;
    if( style ) div.style.cssText = style;

    // multipleAppWodrks
    if( children )
    {
        // canvas
        const canvas = document.createElement('canvas');
        canvas.id = '__canvas__';
        document.body.appendChild( canvas );
        document.body.insertBefore( canvas, div );

        // children
        children.forEach( child =>
        {
            const { style, url } = child;
            child = document.createElement('div');
            if( url )   child.dataset.url = url;
            if( style ) child.style.cssText = style;
            div.appendChild( child );
        });

        multipleAppWodrks( id, canvas.id, modelKey );
    }

    // singleAppWorks
    else
    {
        singleAppWorks( id, modelKey );
    }
}

//==============================================================================

export { renderModels };